<template>
<nav-bar>
  <span slot="title" class="title">台桌管理 - {{room.name}}</span>
</nav-bar>
<div class="content-form">
  <form @submit="update" action="/cpanel/rooms" accept-charset="UTF-8" method="post">
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label">分类名称</label>
      <div class="col-sm-10">
        <input class="form-control" placeholder="名称" type="text" v-model="room.name" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label">台桌人数</label>
      <div class="col-sm-10">
        <input class="form-control" placeholder="人数" type="text" v-model="room.seat" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
      <div class="col-sm-10">
        <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
      </div>
    </div>
  </form>
</div>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        room: {
          name: '',
          seat: 0
        }
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/rooms/one.json', { access_token: this.access_token, room_id: params.room_id }).then(function (response) {
          transition.next({
            room: response.data
          })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      update () {
        this.showLoading()
        var params = this.$route.params
        this.$http.put('http://jiancan.me/api/m1/rooms/one.json', { access_token: this.access_token, room_id: params.room_id, room: this.room }).then(function (response) {
          this.$route.router.go({ path: '/rooms' })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



